.story-status
  display: flex
  align-items: center
  justify-content: center
  
  line-height: 1
  cursor: default
  
  width: max-content 
  padding: 4px
  margin: 4px
  
  border: 1px solid rgba(0, 0, 0, .7)
  color: rgba(0, 0, 0, .7)
  &:hover
    opacity: 0.9
    color: rgba(0, 0, 0, .8)
  
  
  &.inprogress
    background-color: rgba(0, 116, 255, .5)
  &.completed
    background-color: rgba(0, 241, 0, .5)
  &.onhiatus
    background-color: rgba(127, 127, 127, .5)
  &.cancelled
    background-color: rgba(255, 0, 0, .5)
    
  &>i.material-icons-outlined
    margin-right: .1rem
    font-size: 1rem
    